<template>
  <div class="list">
    <div class="clearfix list-button">
      <div class="pull-left">
        <h4 class="list-button-text">合同基本信息：</h4>
      </div>
      <div class="pull-right" style="margin-right: 10px">
        <el-button   @click="">删除</el-button>
        <el-button   @click="">保存</el-button>
      </div>
    </div>
    <div class="clearfix list-center">
      <!--<h-table></h-table>-->
      <el-col :span="12">
        <el-form :model="ruleForm" :rules="rules" ref="ruleForm" label-width="120px" class="demo-ruleForm">
          <el-form-item label="合同编号" >
            <el-input v-model="ruleForm.id" readonly v-if="ruleForm.editorId"></el-input>
            <p class="list-editor"  v-else >{{ruleForm.id}}</p>
          </el-form-item>
          <el-form-item label="项目名称" prop="name">
            <el-input v-model="ruleForm.name"
                      @blur="editorForm(ruleForm.editorName,'editorName')"
                      v-focus="ruleForm.editorName"
                      v-if="ruleForm.editorName">
            </el-input>
            <p class="list-editor" @click="editorForm(ruleForm.editorName,'editorName')" v-else>
              {{ruleForm.name}}
            </p>
          </el-form-item>
          <el-form-item label="客户名称" prop="customer">
            <el-select v-model="ruleForm.customer" v-if="ruleForm.editorCustomer"
                       @blur="editorForm(ruleForm.editorCustomer,'editorCustomer')"
                       @visible-change="selectDropdown"
                       v-focus="ruleForm.editorCustomer"
                       placeholder="请选择客户名称" style="display: block">
              <el-option
                v-for="item in customerOptions"
                :key="item.value"
                :label="item.label"
                :value="item.value">
              </el-option>
            </el-select>
            <p class="list-editor" v-else @click="editorForm(ruleForm.editorCustomer,'editorCustomer')">
              {{ruleForm.customer}}
            </p>
          </el-form-item>
          <el-form-item label="签署时间" required>
            <el-date-picker v-model="ruleForm.date"  type="date"
                            @blur="editorForm(ruleForm.editorDate,'editorDate')"
                            v-focus="ruleForm.editorDate"
                            v-if="ruleForm.editorDate">
            </el-date-picker>
            <p class="list-editor" v-else @click="editorForm(ruleForm.editorDate,'editorDate')">
              {{ruleForm.date | timeFormat}}
            </p>
          </el-form-item>
          <el-form-item label="合同金额" prop="money">
            <el-input v-model="ruleForm.money"
                      @blur="editorForm(ruleForm.editorMoney,'editorMoney')"
                      v-focus="ruleForm.editorMoney"
                      v-if="ruleForm.editorMoney">
            </el-input>
            <p class="list-editor" @click="editorForm(ruleForm.editorMoney,'editorMoney')" v-else>
              {{ruleForm.money}}
            </p>
          </el-form-item>
          <el-form-item label="执行状态" prop="state">
            <el-select v-model="ruleForm.state"  v-if="ruleForm.editorState"
                       @blur="editorForm(ruleForm.editorState,'editorState')"
                       v-focus="ruleForm.editorState"
                       @visible-change="selectDropdown"
                       placeholder="请选择执行状态" style="display: block"
                       :popper-append-to-body="false">
              <el-option
                v-for="item in stateOptions"
                :key="item.value"
                :label="item.label"
                :value="item.value">
              </el-option>
            </el-select>
            <p class="list-editor" v-else @click="editorForm(ruleForm.editorState,'editorState')">
              {{setValueLable(ruleForm.state,stateOptions)}}
            </p>
          </el-form-item>
          <el-form-item label="渠道" prop="channel">
            <el-select v-model="ruleForm.channel" v-if="ruleForm.editorChannel"
                       @blur="editorForm(ruleForm.editorChannel,'editorChannel')"
                       @visible-change="selectDropdown"
                       v-focus="ruleForm.editorChannel"
                       placeholder="请选择渠道" style="display: block">
              <el-option
                v-for="item in channelOptions"
                :key="item.value"
                :label="item.label"
                :value="item.value">
              </el-option>
            </el-select>
            <p class="list-editor" v-else @click="editorForm(ruleForm.editorChannel,'editorChannel')">
              {{ruleForm.channel}}
            </p>
          </el-form-item>
          <el-form-item label="联系人" prop="contact">
            <el-select v-model="ruleForm.contact" v-if="ruleForm.editorContact"
                       @blur="editorForm(ruleForm.editorContact,'editorContact')"
                       @visible-change="selectDropdown"
                       v-focus="ruleForm.editorContact"
                       placeholder="请选择联系人" style="display: block">
              <el-option
                v-for="item in contactOptions"
                :key="item.value"
                :label="item.label"
                :value="item.value">
              </el-option>
            </el-select>
            <p class="list-editor" v-else @click="editorForm(ruleForm.editorContact,'editorContact')">
              {{ruleForm.contact}}
            </p>
          </el-form-item>
          <el-form-item label="联系人手机号" >
            <el-input v-model="ruleForm.phone" readonly v-if="ruleForm.editorPhone"></el-input>
            <p class="list-editor"  v-else >{{ruleForm.phone}}</p>
          </el-form-item>
          <el-form-item label="销售负责人" prop="person">
            <el-select v-model="ruleForm.person"  v-if="ruleForm.editorPerson"
                       @blur="editorForm(ruleForm.editorPerson,'editorPerson')"
                       @visible-change="selectDropdown"
                       v-focus="ruleForm.editorPerson"
                       placeholder="请选择销售负责人" style="display: block">
              <el-option
                v-for="item in personOptions"
                :key="item.value"
                :label="item.label"
                :value="item.value">
              </el-option>
            </el-select>
            <p class="list-editor" v-else @click="editorForm(ruleForm.editorPerson,'editorPerson')">
              {{ruleForm.person}}
            </p>
          </el-form-item>
        </el-form>
      </el-col>
    </div>
    <div class="clearfix list-button">
      <div class="pull-left">
        <h4 class="list-button-text">备注：</h4>
      </div>
    </div>
    <div class="list-center-bottom" style="height: 140px;border: 1px solid #dfdfdf;background: #fafafa">
      <el-input type="textarea" :rows="8"
                placeholder="请输入内容"
                @blur="editorForm(editorRemark,'editorRemark')"
                v-focus="editorRemark"
                v-model="textarea" v-if="editorRemark"></el-input>
      <div class="list-remarks" @click="editorForm(editorRemark,'editorRemark')" v-else>
        {{textarea}}
      </div>
    </div>
  </div>
</template>
<script>
  import HTable from '@/components/layout/table/HTable'
  export default {
    name: 'chanceDetail',
    components: {
      HTable
    },
    data () {
      return {
        ruleForm: {
          index: '',
          editorId: true,
          name: '',
          editorName: true,
          customer: '',
          editorCustomer: true,
          date: '',
          editorDate: true,
          money: '',
          editorMoney: true,
          state: '',
          editorState: true,
          channel: '',
          editorChannel: true,
          contact: '',
          editorContact: true,
          phone: '',
          editorPhone: true,
          person: '',
          editorPerson: true
        },
        rules: {
          name: [
            { required: true, message: '请输入项目名称', trigger: 'blur' }
          ],
          customer: [
            { required: true, message: '请选择客户名称', trigger: 'blur' }
          ],
          money: [
            { required: true, message: '请输入合同金额', trigger: 'blur' }
          ],
          state: [
            { required: true, message: '请选择执行状态', trigger: 'blur' }
          ],
          channel: [
            { required: true, message: '请选择渠道', trigger: 'change' }
          ],
          contact: [
            { required: true, message: '请选择联系人', trigger: 'change' }
          ],
          person: [
            { required: true, message: '请选择销售负责人', trigger: 'change' }
          ]
        },
        stateOptions: [{
          value: 'state2',
          label: '未开始'
        }, {
          value: 'state3',
          label: '执行中'
        }, {
          value: 'state4',
          label: '已交付'
        }, {
          value: 'state5',
          label: '已关闭'
        }],
        customerOptions: [],
        channelOptions: [],
        contactOptions: [],
        personOptions: [],
        type: 'create',
        textarea: '',
        editorRemark: true,
        dropdown: false,
        dropValue: '',
        dropObj: ''
      }
    },
    watch: {
      /**
       * @params 监听参数变化重新获取数据
       * */
      params: {
        handler (val) {
          this.getData(val)
        },
        deep: true
      }
    },
    computed: {
      state () {
        return this.$store.state.app
      }
    },
    methods: {
      editorForm (value, obj) {
        if (this.type === 'detail') {
          if (obj === 'editorRemark') {
            this.editorRemark = !value
          } else {
            this.dropValue = value
            this.dropObj = obj
            if (this.dropdown === true) {
              this.ruleForm[obj] = value
            } else {
              this.ruleForm[obj] = !value
              if (event.type === 'blur') {
                if (obj === 'editorName') {
                  if (this.ruleForm.name === '') {
                    this.ruleForm[obj] = value
                  }
                }
              }
            }
          }
        }
      },
      setValueLable (state, arr) {
        let label = ''
        arr.forEach(function (val) {
          if (val.value === state) {
            label = val.label
          }
        })
        return label
      },
      selectDropdown (value) {
        this.dropdown = value
        if (!value) {
          this.editorForm(this.dropValue, this.dropObj)
        }
      },
      /**
       * @params:category 分类 page：页码 limit:条数
       * */
      getData (params) {
      },
      /**
       * @on-change 页码改变的回调，返回改变后的页码
       * */
      pageChange (page) {
        this.params.page = page
      },
      /**
       * @on-page-size-change 切换每页条数时的回调，返回切换后的每页条数
       * */
      PageSizeChange (limit) {
        this.params.limit = limit
      },
      /**
       * 表格对应操作方法
       * @show 查看
       * @remove 删除
       * @edit
       */
      remove (index) {
        this.listData.splice(index, 1)
      },
      edit (index) {
      },
      /**
       * 多选
       * selection：已选项数据 row：刚选择的项数据
       */
      onSelect (selection, row) {
        // console.log(selection,row)
      },
      onSelectionChange (selection) {
        this.selection = selection
      }
    },
    created () {
      this.getData(this.params)
      if (this.$route.query.type === 'detail') {
        this.type = 'detail'
        this.ruleForm = {
          id: '1',
          editorId: false,
          name: 'xxxx开发项目',
          editorName: false,
          customer: '王二麻子',
          editorCustomer: false,
          date: new Date(2018, 10, 10, 10),
          editorDate: false,
          money: '1,000,000',
          editorMoney: false,
          state: 'state2',
          editorState: false,
          channel: '橙悦小程序',
          editorChannel: false,
          contact: '橙悦',
          editorContact: false,
          phone: '1331313131313',
          editorPhone: false,
          person: '李四',
          editorPerson: false
        }
        this.editorRemark = false
      }
    },
    mounted () {
    }
  }
</script>



